	module.exports = {"data":{"markdownRemark":{"html":"<h2>Variants</h2>\n<p>\"weight\" variants for each primary color value for 100-900 shades of the initial color, with 500 being the initial color and 100-400 being variants of the tint going lighter and 600-900 being variants of the shade going darker22</p>\n<h3>Palette</h3>\n<ul>\n<li>Primary</li>\n<li>Secondary</li>\n<li>Tertiary</li>\n</ul>\n<h3>Shades</h3>\n<ul>\n<li>Light</li>\n<li>Grey</li>\n<li>Dark</li>\n</ul>\n<h3>Indicators</h3>\n<ul>\n<li>Info</li>\n<li>Success</li>\n<li>Warning</li>\n<li>Error</li>\n</ul>\n<h2>States</h2>\n<ul>\n<li>Hover</li>\n<li>Active</li>\n</ul>\n<h2>Color fucntions</h2>","frontmatter":{"path":"/color","tags":["color"],"title":"Color"}}},"pathContext":{"prev":{"excerpt":"Constraints Approach Less is more:  the download payload for all styles, assets, and markup should be minised as much as possible. Keep it simple:  all styles should be defined using simple, modern, and understandable  CSS  properties, this means no…","html":"<h2>Constraints</h2>\n<ul>\n<li></li>\n</ul>\n<h2>Approach</h2>\n<ol>\n<li><em>Less is more:</em> the download payload for all styles, assets, and markup should be minised as much as possible.</li>\n<li><em>Keep it simple:</em> all styles should be defined using simple, modern, and understandable <abbr title=\"Cascading Style Sheets\">CSS</abbr> properties, this means no opinionated development workflows such as Sass, Less etc.</li>\n<li><em>Effecient design is good design:</em> whilst we should minimise technical understanding, this should not interfere with reusable well defined code, therefore we will utilise PostCSS, Autoprefixer and CSSNext to automate cross-browser compatibility.</li>\n<li><em>Legibility is key:</em> we use well structured typefaces &#x26; semantics HTML tags with WACG 2.0 compliant contrast ratios to emphasis content.</li>\n<li><em>Easy reading:</em> we will use a high contrast color palette (<abbr title=\"Web Content Accessibility Guidelines 2.0 AAA\">WCAG 2.0 AAA</abbr>) and scaling vertical rythym to provide a clear visual heriarchy context.</li>\n</ol>\n<h2>Objectives</h2>\n<ul>\n<li><em>Clarity:</em> we will measure the clarity of the typographic design through automatic accessibility testing, ensuring culor contrast and DOM tree hierarchy pass WCAG 2.0 AAA guidelines</li>\n<li><em>Consistancy:</em> we will ensure the consistant rendering of the design across browsers, mediums (screens and print), and devices.</li>\n</ul>\n<h2>Writing scalable &#x26; reusable CSS</h2>\n<h3>Relationship between HTML and CSS</h3>\n<h4>CSS as presentation</h4>\n<h4>Selectors</h4>\n<h5>Class selectors</h5>\n<p>Classname selectors (<code>.classname</code>) are preferred due to their low specificity and semantic meaning.</p>\n<h5>Element selectors</h5>\n<p>Element selectors (<code>h2</code>) are used for reseting or applying the global scope CSS properties to HTML elements, primary for typesetting.</p>\n<h5>State selectors</h5>\n<p><code>:hover</code></p>\n<p><code>:focus</code>  </p>\n<p><code>:active</code></p>\n<p><code>:disabled</code></p>\n<h3>Utilising the Cascade</h3>\n<h4>Current Color and Color/Background inheritance</h4>\n<h2>Testing CSS</h2>\n<h3>Code testing</h3>\n<h3>Visual Testing</h3>","id":"/Users/bigfish/Projects/guide/src/pages/css/index.md absPath of file >>> MarkdownRemark","timeToRead":1,"frontmatter":{"path":"/css","tags":["css"],"title":"CSS"}},"next":{"excerpt":"This is a lead paragraph with dominant styles that make it stand out from the content below, this can be used to introduce the follwoing content and provide context for people skimming page content This is a standard paragraph, it makes up the…","html":"<p>This is a lead paragraph with dominant styles that make it stand out from the content below, this can be used to introduce the follwoing content and provide context for people skimming page content</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 750px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.68%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"alt text\"\n        title=\"Logo Title Text 1\"\n        src=\"/static/4b685e8104e18224e0a9a59f7d11d6d9-fde49.jpg\"\n        srcset=\"/static/4b685e8104e18224e0a9a59f7d11d6d9-52f6e.jpg 320w,\n/static/4b685e8104e18224e0a9a59f7d11d6d9-5cb1f.jpg 640w,\n/static/4b685e8104e18224e0a9a59f7d11d6d9-fde49.jpg 1280w,\n/static/4b685e8104e18224e0a9a59f7d11d6d9-5460d.jpg 1920w,\n/static/4b685e8104e18224e0a9a59f7d11d6d9-d4800.jpg 2500w\"\n        sizes=\"(max-width: 750px) 100vw, 750px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>This is a standard paragraph, it makes up the majority of page content. Paragraphs should be short in length and use a tone that makes content easily discoverable. This can be achieved with text formatting such as super text along with sub text or using text styles to highlight content using <strong>strong (or bold) text</strong> or <em>emphasis (or italic) text</em>, or maybe underline text, as well as strikethrough text for redacted content.</p>\n<blockquote>\n<p>You can even include a quote which will break up content that surrounds it.</p>\n</blockquote>\n<h3>A level 3 heading</h3>\n<p>Using headings to breakup content is the simplist and most effective way to draw the user's attention to each context within a page. It also helps break up content visually which makes larger content easier to read.</p>\n<p>Sometimes content will need to be formatted in other ways including:</p>\n<ul>\n<li>Unordered lists</li>\n<li>with mutiple</li>\n<li>list items.</li>\n</ul>\n<p>As well as:</p>\n<ol>\n<li>Ordered lists</li>\n<li>with mutiple</li>\n<li>list items.</li>\n</ol>\n<p>Text content can also have <a href=\"#\">links</a> which should be visually identifiable from other content either through contrast (colour) or using underlines.</p>","id":"/Users/bigfish/Projects/guide/src/pages/example/index.md absPath of file >>> MarkdownRemark","timeToRead":1,"frontmatter":{"path":"/example","tags":["example"],"title":"Example"}}}}

